.skills-container {
  background-color: #92a2c2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 5px;
  width: 80%;
  border-radius: 10px;
}
.skills-container {
    flex-direction: row;
    flex-wrap: wrap;
}
.skill-card {
  background-color: #f0f0f0;
  text-align: center;
  border-radius: 50%; /* Makes the shape oval */
  padding: 20px;
  display: inline-block;
  margin: 10px;
  overflow: hidden;
  width: 40px; /* Set a fixed width */
  height: 40px; /* Set a fixed height */
  box-shadow: 0 4px 8px rgba(255, 0, 0, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(1px + 0.5vw); /* Adjust font size dynamically */
  word-wrap: break-word; /* Ensure text wraps if necessary */
  text-overflow: clip; /* Prevent ellipsis */
}

.skill-card:hover {
  transform: scale(1.25);
  transition: transform 0.3s ease, background-color 0.3s ease;
}
