.education-container{
    border-radius: 10px;
    background-color: #92a2c2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 5px;
    width: 80%;
}

.education-item {
    background-color: #f0f0f0;
    border-radius: 10px;
    padding: 20px;
    margin: 10px;
    width: 90%;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}
.education-item h2 {
    font-size: 1.5rem;
    color: #333;
    margin-bottom: 10px;
    font-weight: bold;
}
.education-item p {
    font-size: 1.2rem;
    color: #555;
    line-height: 1.6;
    margin: 0;
}
